* {
  font-family: "Fjalla One", sans-serif;
}

.home {
  height: 100vh;
}
.home img {
  width: 100%;
  height: 100%;
  transform: scaleX(-1);
  position: absolute;
}

.home h1 {
  position: relative;
  top: 3rem;
  left: 1rem;
  margin-right: 1rem;
  color: white;
  font-size: 24vh;
  font-weight: 800;
  letter-spacing: 0.25rem;
}
.home p {
  position: relative;
  top: 1rem;
  left: 3rem;
  margin-right: 3rem;
  color: white;
  font-size: 2rem;
  font-family: "Fjalla One", sans-serif;
}
.downNavigation {
  color: white;
  position: absolute;
  left: 45vw;
  bottom: -20vh;
}
.downNavigation:focus {
  outline: none;
  box-shadow: none;
}
.vert-move {
  -webkit-animation: mover 1s infinite alternate;
  animation: mover 1s infinite alternate;
}
@keyframes mover {
  0% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(-10px);
  }
}
.me {
  width: 80%;
  border-radius: 50%;
  margin-bottom: 2rem;
}
.about {
  padding: 15vh 10vw;
  text-align: center;
  min-height: 100vh;
}
.whoAmI {
  margin-bottom: 10vh;
  line-height: 2.5rem;
  font-size: 2rem;
}
.carousel-control-prev-icon,
.carousel-control-next-icon {
  height: 0;
  width: 0;
  background-size: 0;
  border-radius: 0;
  background-image: none;
}
.carousel-control-next-icon:after {
  content: ">";
  font-size: 55px;
  color: #562349;
  margin-right: -5rem;
}

.carousel-control-prev-icon:after {
  content: "<";
  font-size: 55px;
  color: #562349;
  margin-left: -5rem;
}
.carouselMobile {
  margin-left: 5vw;
}
.carousel-item {
  text-align: center;
}
.mongo {
  margin-bottom: 1rem;
}
.about h1 {
  margin-bottom: 1rem;
}
.projectSection {
  padding: 10vh 10vw;
  color: black;
  background: #222;
}
.projectSection h1 {
  margin-bottom: 10vh;
  color: white;
}
.projects {
  margin: 4rem 3vw;
  height: 100%;
  box-shadow: 2px 5px 18px black;
}
.projects img {
  margin-top: 1rem;
  height: 10rem;
}
.projects h2 {
  margin-top: 1rem;
}
.row {
  width: 100%;
}
.middle {
  padding: 0 1rem 0 1rem;
}
.mobile {
  background-color: #fff;
}
.projectRepository {
  position: relative;
  width: 12rem;
  height: 4rem;
  top: 10vh;
  margin-bottom: 2rem;
  font-size: 2rem;
  color: white;
}
.dark {
  color: #9c9c9c;
}

.contact {
  background-color: #131313;
  color: white;
}

.columna {
  padding: 3vh 5vw;
}
.icon {
  margin: 2vh;
}
.anchor {
  color: white;
}
.anchor:hover {
  text-decoration: none;
  color: rgb(136, 135, 135);
}
.columna p {
  font-size: 3vw;
}
.fullWidth {
  width: 100vw;
}
.contactMobile {
  background-color: #131313;
  color: white;
  text-align: center;
}
.link:hover {
  cursor: pointer;
}
.blue {
  color: #45046a;
}
.blue:hover {
  text-decoration: none;
}
